@import url('https://fonts.googleapis.com/css2?family=Merriweather+Sans:wght@300;700&family=Merriweather:wght@300;700&display=swap');

/* Global Styles */
:root {
    --primary-color: #7a9492;
    --link-color: #ffa14e;
    --dark-color: #4d5553;
    --secondary-dark-color: #100f0eaa;
    --light-color: #ffffd9;
    --danger-color: #dc3545;
    --success-color: #28a745;

    --primary-font: 'Merriweather', serif;
    --secondary-font: 'Merriweather Sans', sans-serif;
}
html,
body {
    font-size: 14px;
}
#root {
    overflow-x: hidden;
    overflow-y: scroll;
    width: 100vw;
    height: 100vh;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
}
input {
    background-color: transparent;
}
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
    -webkit-transition-delay: 99999s;
}
input:-webkit-autofill {
    -webkit-text-fill-color: var(--primary-color);
    -webkit-box-shadow: 0 0 0 100px transparent inset;
    font-family: var(--primary-font) !important;
}

h2.MuiTypography-root {
    text-align: center;
    font-size: 1.5em;
    color: var(--primary-color);
}
.MuiSvgIcon-root.MuiSvgIcon-fontSizeMedium {
    vertical-align: bottom;
}

.animatedRoute {
    position: absolute;
    width: 100%;
    transform-origin: 50% 0%;
    padding-bottom: 80px;
}

.porchlightLogo {
    max-height: 40vh;
    width: auto;
}

.drawerListItems a {
    width: 100%;
    padding: 8px 16px;
    display: flex;
}

.squareGrid {
    /* The content width you use on your website */
    --content-width: 80vw;

    /* The size of the gutter */
    --gutter: 40px;

    /* The amount of columns */
    --columns: 1;

    /* This is the calculation for the row height. */
    --row-size: calc(
        (var(--content-width) - (var(--gutter) * (var(--columns) - 1))) /
            var(--columns)
    );

    display: grid;

    width: 100%;
    max-width: var(--content-width);

    grid-template-columns: repeat(var(--columns), 1fr);
    grid-auto-rows: var(--row-size);

    grid-column-gap: var(--gutter);
    grid-row-gap: var(--gutter);
}

.bookingDetails strong {
    color: var(--light-color);
    /* text-decoration: underline; */
}
.bookingDetails .MuiSvgIcon-root {
    color: var(--light-color);
    display: block;
    margin: 0 auto;
    width: 30px;
    height: 30px;
}
.bookingDetails .MuiDivider-fullWidth {
    margin: 8px 16px;
}
.MuiGrid-item .MuiChip-root {
    margin-bottom: 4px;
}

.inactive-host {
    /* Host Matrix */
    opacity: 0.3;
    filter: blur(0.8px);
}

.diffTime {
    color: var(--light-color);
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    font-family: var(--primary-font);
    font-size: 1rem;
    line-height: 1.6;
    background-color: var(--dark-color);
    background-image: linear-gradient(180deg, #1b1f1ef6, #1b1f1eef),
        url(./img/Porchlight_bg_design-01.png) !important;
    background-size: 100%, 60%;
    background-position: top center;
    background-repeat: repeat;
    color: #fff;
}

/* Map Styles */

.markerGlyph .count {
    position: absolute;
    display: block;
    top: 5px;
    left: 0;
    right: 0;
    width: 19px;
    margin-left: auto;
    margin-right: auto;
    color: var(--link-color);
    font-size: 1em;
    text-align: center;

    text-shadow: rgb(28, 30, 28) 1px 0px 0px, rgb(28, 30, 28) -1px 0px 0px,
        rgb(28, 30, 28) -1px -1px 0px, rgb(28, 30, 28) -1px 1px 0px,
        rgb(28, 30, 28) 0px 1px 0px, rgb(28, 30, 28) 0px -1px 0px,
        rgb(28, 30, 28) 1px 1px 0px, rgb(28, 30, 28) 1px -1px 0px,
        rgb(28, 30, 28) -1px 1px 0px, rgb(28, 30, 28) 1px -1px 0px;
}
.markerGlyph .fa-circle {
    margin-top: 3px;
    transform: scale(0.9, 0.9);
}

.markerSelected {
    z-index: 1000;
    perspective: 100px;
    perspective-origin: bottom;
}

.yNHHyP-marker-view:not(.markerSelected):hover {
    z-index: 1000;
    perspective: 100px;
    perspective-origin: bottom;
    animation: markerHover 0.3s forwards;
    animation-timing-function: cubic-bezier(0.34, 1.2, 0.64, 1);
    top: 0px;
}

@keyframes markerHover {
    100% {
        top: -2px;
    }
}

.markerSelected .markerGlyph .count,
.yNHHyP-marker-view:hover .markerGlyph .count {
    text-shadow: #fff 1px 0px 0px, #fff -1px 0px 0px, #fff 0px 1px 0px,
        #fff 0px -1px 0px, #fff 1px 1px 0px, #fff 1px -1px 0px,
        #fff -1px 1px 0px, #fff 1px -1px 0px;
}

.markerSelected:after,
.yNHHyP-marker-view:hover:after {
    content: attr(title);
    opacity: 0;
    transform: translate3d(-50%, -50px, -200px) rotateX(90deg);
    /* transform: translate3d(-50%, -65px, 10px) rotateX(0deg); */
    transform-origin: 0 center -80px;
    position: absolute;
    display: block;
    width: auto;
    text-align: center;
    text-wrap: nowrap;
    left: 50%;
    font-size: 22px;
    text-shadow: rgb(28, 30, 28) 3px 0px 0px,
        rgb(28, 30, 28) 2.83487px 0.981584px 0px,
        rgb(28, 30, 28) 2.35766px 1.85511px 0px,
        rgb(28, 30, 28) 1.62091px 2.52441px 0px,
        rgb(28, 30, 28) 0.705713px 2.91581px 0px,
        rgb(28, 30, 28) -0.287171px 2.98622px 0px,
        rgb(28, 30, 28) -1.24844px 2.72789px 0px,
        rgb(28, 30, 28) -2.07227px 2.16926px 0px,
        rgb(28, 30, 28) -2.66798px 1.37182px 0px,
        rgb(28, 30, 28) -2.96998px 0.42336px 0px,
        rgb(28, 30, 28) -2.94502px -0.571704px 0px,
        rgb(28, 30, 28) -2.59586px -1.50383px 0px,
        rgb(28, 30, 28) -1.96093px -2.27041px 0px,
        rgb(28, 30, 28) -1.11013px -2.78704px 0px,
        rgb(28, 30, 28) -0.137119px -2.99686px 0px,
        rgb(28, 30, 28) 0.850987px -2.87677px 0px,
        rgb(28, 30, 28) 1.74541px -2.43999px 0px,
        rgb(28, 30, 28) 2.44769px -1.73459px 0px,
        rgb(28, 30, 28) 2.88051px -0.838247px 0px;
    animation: markerPopup 0.3s forwards;
    animation-timing-function: cubic-bezier(0.34, 1.2, 0.64, 1);
}

@keyframes markerPopup {
    100% {
        opacity: 1;
        transform: translate3d(-50%, -65px, 10px) rotateX(0deg);
    }
}

.GMAMP-maps-pin-view {
    transform: scale(1, 1);
    transition: transform 0.5s cubic-bezier(0.23, 1, 0.32, 1) 0.05s;
}
.GMAMP-maps-pin-view.selected {
    transform: scale(1.2, 1.2);
}
.GMAMP-maps-pin-view.selected path.RIFJN-maps-pin-view-border,
.GMAMP-maps-pin-view.selected path.KWCFZI-maps-pin-view-default-glyph,
.GMAMP-maps-pin-view.selected .markerGlyph {
    fill: white;
    color: white;
}

.GMAMP-maps-pin-view:hover path.RIFJN-maps-pin-view-border,
.GMAMP-maps-pin-view:hover path.KWCFZI-maps-pin-view-default-glyph,
.GMAMP-maps-pin-view:hover .markerGlyph {
    fill: white;
    color: white;
}

/* give dialog boxes the porchlight background */
.porchlightBG .MuiPaper-root {
    background-color: var(--dark-color);
    background-image: linear-gradient(180deg, #1b1f1ef6, #1b1f1eef),
        url(./img/Porchlight_bg_design-01.png) !important;
    background-size: 600px;
    background-position: top center;
    background-repeat: repeat;
}

.spinner {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    height: 100vh;
}

a {
    color: var(--link-color);
    text-decoration: none;
}

ul {
    list-style: none;
}

.termsAndConditions ul {
    list-style: disc;
    margin-left: 20px;
    line-height: 1.7;
}
.termsAndConditions li,
.termsAndConditions p {
    margin-bottom: 8px;
}

img {
    width: 100%;
}

/* Event Card  */
.eventCardSvgBorder {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}
.eventCardSvgBorder.eventDraft {
    top: -4px;
    left: -4px;
}

.eventCardSvgBorder polygon {
    fill: transparent;
    transition: all 1s ease;
    pointer-events: none;
    transition-delay: 0.3s;
}
.eventCardSvgBorder .borderEffect {
    stroke: var(--light-color);
    stroke-width: 2;
    fill: transparent;
}
.bookingWhen {
    border: 1px solid transparent;
    transition: all 0.8s cubic-bezier(0.23, 1, 0.32, 1) 0ms;
    transition-delay: 0s;
    box-shadow: 0 0 0 0 transparent;
}

.bookingWhen.mostRecent {
    /* border: 1px solid var(--dark-color); */
    transition-delay: 0.3s;
    box-shadow: 0 0 5px 0px var(--dark-color);
}

/* Utilities */
.container {
    /* max-width: 1100px; */
    margin: auto;
    /* overflow: hidden; */
    /* padding: 0 2rem; */
    /* margin-top: 6rem;
	margin-bottom: 3rem; */
    position: relative;
}

/* Text Styles*/
.x-large {
    font-size: 4rem;
    line-height: 1.2;
    margin-bottom: 1rem;
}

.large {
    font-size: 3rem;
    line-height: 1.2;
    margin-bottom: 1rem;
}

.lead {
    font-size: 1.5rem;
    margin-bottom: 1rem;
}

.text-center {
    text-align: center;
}

.text-primary {
    color: var(--primary-color);
}

.text-dark {
    color: var(--dark-color);
}

/* Padding */
.p {
    padding: 0.5rem;
}
.p-1 {
    padding: 1rem;
}
.p-2 {
    padding: 2rem;
}
.p-3 {
    padding: 3rem;
}
.py {
    padding: 0.5rem 0;
}
.py-1 {
    padding: 1rem 0;
}
.py-2 {
    padding: 2rem 0;
}
.py-3 {
    padding: 3rem 0;
}

/* Margin */
.m {
    margin: 0.5rem;
}
.m-1 {
    margin: 1rem;
}
.m-2 {
    margin: 2rem;
}
.m-3 {
    margin: 3rem;
}
.my {
    margin: 0.5rem 0;
}
.my-1 {
    margin: 1rem 0;
}
.my-2 {
    margin: 2rem 0;
}
.my-3 {
    margin: 3rem 0;
}

.btn,
button {
    display: inline-block;
    background: var(--light-color);
    color: #333;
    padding: 0.6rem 1.8rem;
    font-family: var(--secondary-font);
    text-transform: uppercase;
    font-weight: bold;
    font-style: normal;
    text-decoration: none;
    letter-spacing: 1px;
    line-height: 1.8;

    font-size: 0.8rem;
    border: none;
    cursor: pointer;
    margin-right: 0.5rem;
    margin-bottom: 0.5rem;
    transition: opacity 0.2s ease-in;
    outline: none;
}

.badge {
    font-size: 0.8rem;
    padding: 0.1rem;
    text-align: center;
    margin: 0.3rem;
    background: var(--light-color);
    color: #333;
}

.alert {
    padding: 0.4rem 0.8rem;
    margin: 0 0;
    opacity: 0.9;
    background: var(--light-color);
    color: #333;
    max-width: 100%;
}

.btn-primary,
.bg-primary,
.badge-primary,
.alert-primary {
    background: var(--primary-color);
    color: #fff;
}

.btn-light,
.bg-light,
.badge-light,
.alert-light {
    background: var(--light-color);
    color: #333;
}

.btn-dark,
.bg-dark,
.badge-dark,
.alert-dark {
    background: var(--secondary-dark-color);
    color: #fff;
}

.btn-danger,
.bg-danger,
.badge-danger,
.alert-danger {
    background: var(--danger-color);
    color: #fff;
}

.btn-success,
.bg-success,
.badge-success,
.alert-success {
    background: var(--success-color);
    color: #fff;
}

.btn-white,
.bg-white,
.badge-white,
.alert-white {
    background: #fff;
    color: #333;
    border: #ccc solid 1px;
}

.active {
    border-color: #28a745;
    border-style: double;
    border-width: 4px;
}

.btn:hover {
    opacity: 0.8;
}

.bg-light,
.badge-light {
    border: #ccc solid 1px;
}

.round-img {
    border-radius: 50%;
}

.line {
    height: 1px;
    background: #ccc;
    margin: 1.5rem 0;
}

/* Overlay */
.dark-overlay {
    background-color: rgba(0, 0, 0, 0.7);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

/* Forms */
.form .form-group {
    margin: 1.2rem 0;
}

.form .form-text {
    display: block;
    margin-top: 0.3rem;
    color: #888;
}

input[type='text'],
input[type='email'],
input[type='password'],
input[type='date'],
select,
textarea {
    display: block;
    width: 100%;
    /* padding: 0.4rem; */
    font-size: 1.2rem;
    border: none;
    border-radius: 3px;
}

.form input[type='submit'],
button {
}

.form .social-input {
    display: flex;
}

.form .social-input i {
    padding: 0.5rem;
    width: 4rem;
}

.form .social-input i.fa-twitter {
    color: #38a1f3;
}
.form .social-input i.fa-facebook {
    color: #3b5998;
}
.form .social-input i.fa-instagram {
    color: #3f729b;
}
.form .social-input i.fa-youtube {
    color: #c4302b;
}
.form .social-input i.fa-linkedin {
    color: #0077b5;
}

.table th,
.table td {
    padding: 1rem;
    text-align: left;
}

.table th {
    background: var(--light-color);
}

/* Navbar */
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.7rem 2rem;
    position: fixed;
    z-index: 1;
    width: 100%;
    top: 0;
    border-bottom: solid 1px var(--primary-color);
    opacity: 0.9;
}

.navbar ul {
    display: flex;
}

.navbar a {
    color: #fff;
    padding: 0.45rem;
    margin: 0 0.25rem;
}

.navbar a:hover {
    color: var(--primary-color);
}

.navbar .welcome span {
    margin-right: 0.6rem;
}

/* Landing Page */
.landing {
    position: relative;
    /* background-image: linear-gradient(
      180deg,
      rgba(122, 148, 146, 0.9) 0%,
      rgba(122, 148, 146, 0.8) 100%
    ),
    url(./img/Porchlight_bg_design-01.png) !important;
  background-size: contain;
  background-repeat: repeat;
  height: 100vh; */
}

.landing-inner {
    color: #fff;
    height: 100%;
    width: 80%;
    margin: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}

/* Profiles Page */
.profile {
    display: grid;
    grid-template-columns: 2fr 4fr;
    align-items: center;
    grid-gap: 2rem;
    padding: 1rem;
    line-height: 1.8;
    margin-bottom: 1rem;
}

.profileImage {
    grid-column-start: 1;
}
.profileBtn {
    grid-column-start: 1;
}
.artist-info {
    grid-column-start: 2;
    grid-row-start: 1;
}
.artist-info .bio {
    margin: 1rem 0;
}
.artistAdminButtons {
    grid-column-start: 1;
    grid-row-start: 2;
    width: 100%;
}
.artistAdminButtons .btn {
    width: 100%;
}
.form {
    grid-column-start: 2;
    grid-row-start: 2;
    grid-row-end: span 2;
}

/* Profile Page */
.profile-grid {
    display: grid;
    grid-template-areas:
        'top top'
        'about about'
        'exp edu'
        'github github';
    grid-gap: 1rem;
}

.profile-top {
    grid-area: top;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.profile-top img {
    width: 250px;
}

.profile-top .icons a {
    color: #fff;
    margin: 0 0.3rem;
}

.profile-top .icons a:hover {
    color: var(--dark-color);
}

.profile-about {
    grid-area: about;
    text-align: left;
}
.profile-about h2 {
    text-align: center;
}

.profile-about .skills {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.profile-exp {
    grid-area: exp;
}

.profile-edu {
    grid-area: edu;
}

.profile-exp h2,
.profile-edu h2 {
    margin-bottom: 1rem;
}

.profile-exp > div,
.profile-edu > div {
    margin-bottom: 1rem;
    padding-bottom: 1rem;
    border-bottom: #ccc 1px dotted;
}

.profile-exp > div:last-child,
.profile-edu > div:last-child {
    border: 0;
}

.profile-exp p,
.profile-edu p {
    margin: 0.5rem 0;
}

.profile-github {
    grid-area: github;
}

.profile-github .repo {
    display: flex;
}

.profile-github .repo > div:first-child {
    flex: 7;
    flex-basis: 70%;
}

.profile-github > div:last-child {
    flex: 3;
    flex-basis: 20%;
}

/* Posts Page */
.post-form .post-form-header {
    background: var(--primary-color);
    padding: 0.5rem;
}

.post {
    /* display: grid;
  grid-template-columns: 1fr 4fr; */
    grid-gap: 2rem;
    align-items: center;
}

.post > div:first-child {
    text-align: center;
}

.post img {
    width: 100px;
}

.post .comment-count {
    background: var(--light-color);
    color: var(--primary-color);
    padding: 0.1rem 0.2rem;
    border-radius: 5px;
    font-size: 0.8rem;
}

.post .post-date {
    color: #aaa;
    font-size: 0.8rem;
    margin-bottom: 0.5rem;
}

/* Mobile Styles */
@media (max-width: 700px) {
    .container {
        margin-top: 1rem;
    }

    .hide-sm {
        display: none;
    }

    /* Text Styles */
    .x-large {
        font-size: 3rem;
    }

    .large {
        font-size: 2rem;
    }

    .lead {
        font-size: 1rem;
    }

    /* Navbar */
    .navbar {
        display: flex;
        text-align: center;
        width: 100vw;
    }

    .navbar ul {
        text-align: center;
        justify-content: center;
    }

    .navbar h1 {
        margin-bottom: 1rem;
    }

    .navbar .welcome {
        display: none;
    }

    /* Profiles Page */
    .profile {
        grid-template-columns: 1fr;
        text-align: center;
    }

    .profile ul {
        display: none;
    }

    /* Profile Page */

    .profile-top img,
    .profile img {
        width: 200px;
        margin: auto;
    }

    .profile-grid {
        grid-template-areas:
            'top'
            'about'
            'exp'
            'edu'
            'github';
    }

    .profile-about .skills {
        flex-direction: column;
    }

    .dash-buttons a {
        display: block;
        width: 100%;
        margin-bottom: 0.2rem;
    }

    .post {
        grid-template-columns: 1fr;
    }
    .post a,
    .post button {
        padding: 0.3rem 0.4rem;
    }
}
